import Bmob from "hydrogen-js-sdk";
let that;
let uid = uni.getStorageSync("uid")
export default {
	components: {},
	data() {
		return {
			noticeState: false,
			user: null,
			vipShow: false,
		};
	},
	onLoad() {
		that = this;
	},
	onShow() {
		that.user = uni.getStorageSync("user");
		that.getNoticeList()
	},
	methods: {
		willCome() {
			uni.showToast({
				title: "敬请期待",
				icon: "none"
			})
		},
		
		//清除全部缓存
		removeStorage(){
			uni.clearStorageSync()
		},

		//得到消息列表
		getNoticeList() {
			const query = Bmob.Query("InfoCenter");
			query.order("-createdAt");
			const query1 = query.equalTo("toUser", '==', uid);
			const query2 = query.equalTo("toUser", '==', "I75puc2O"); //这是系统消息

			query.or(query1, query2);
			query.find().then(res => {
				for (let item of res) {
					if (item.status == false) {
						that.noticeState = true
					}
				}
				const query = Bmob.Query('wc_setting');
				query.get('FtIO888D').then(res => {
					//console.log(res)
					if (that.user.objectId == "aXcyRBaf") {
						that.vipShow = false
					} else {
						that.vipShow = res.isShow
					}
				}).catch(err => {
					console.log(err)
				})

			});
		},
	}
}
